___CSS_LOADER_EXPORT___.push([module.id, `@charset "UTF-8";

* {
  font-family: "Poppins", sans-serif;
}

.grid-center {
  place-content: center;
}

.index-container {
  width: 30rem;
}

.oscar-statue {
  background: url(${___CSS_LOADER_URL_REPLACEMENT_0___}) center / contain no-repeat;
  width: 10rem;
}

.oscar-statue-sm {
  background: url(${___CSS_LOADER_URL_REPLACEMENT_0___}) center / contain no-repeat;
  height: 4rem;
  width: 4rem;
}

.main-title {
  font-size: 4rem;
}

.details-btn:active {
  border: none;
}

.play-btn {
  height: 45px;
  width: 125px;
  box-shadow: -5px 10px 0px 0px #161616;
}

.play-btn:active {
  box-shadow: none;
}

.form-container {
  background-color: #2d3339;
  width: 380px;
}

.invalid-feedback {
  font-size: 12px;
}

.fixed-alert {
  position: fixed;
  right: 2%;
}

.oscar-img {
  height: 15rem;
}

.rooms-container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  place-items: center;
  height: 25rem;
  width: 45rem;
  background-color: #2d3339;
}

.room-icon {
  font-size: 16px;
}

.rooms {
  height: 230px;
}

.nav-link {
  cursor: pointer;
}

.nav-underline {
  font-size: small;
}

.check {
  background-color: green;
}

.close {
  background-color: red;
}

.rounded-btn {
  height: 28px;
  width: 28px;
  border-radius: 50%;
  display: grid;
  place-items: center;
}

.prediction {
  width: 19rem;
}

.results {
  width: 20rem;
}

.previous-category:active,
.next-category:active {
  border: none;
}

.members-guest {
  height: 180px;
}

.nothing {
  background: url("https://www.pngkey.com/png/full/163-1639407_view-samegoogleiqdbsaucenao-1391950301682-don-t-know-meme-drawing.png")
    center / contain no-repeat;
  height: 15rem;
  width: 15rem;
}

.nothing-sm {
  background: url("https://www.pngkey.com/png/full/163-1639407_view-samegoogleiqdbsaucenao-1391950301682-don-t-know-meme-drawing.png")
    center / contain no-repeat;
  height: 10rem;
  width: 10rem;
}

@media only screen and (max-width: 768px) {
  html {
    font-size: 10px;
  }

  .mobile-font {
    font-size: 14px;
  }

  .form-container {
    width: 80%;
  }

  .mobile-input {
    height: 30px;
    font-size: 12px;
  }

  .rooms-container {
    grid-template-columns: none;
    grid-template-rows: 1fr 2fr;
    height: auto;
    width: 90%;
  }

  .room-container {
    flex-direction: column;
    width: 80%;
  }

  .results {
    width: 25rem;
  }
}
